
.menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    z-index: 9999;
    padding: 20px 10px;
    background-color: #324157;
}
.menu a {
    color: #bfcbd9;
    margin: 0 10px;
    text-decoration: none;
}
.menu .router-link-active {
    color: #ff6600;
}



